<template>
  <div class="container">
    <vue-headful
      title="Writing | Rhys Matthew | Web Developer"
      description="Articles by Rhys Matthew covering all things web development, web design and UX."
      keywords="Writing by Rhys Matthew, writing, articles, blog post, HTML, JS, JavaScript, CSS, PHP, Wordpress developer"
      :head="{
        'meta[property=\'og:url\']': {content: 'https://rhys.digital/writing/'},
        'meta[name=\'twitter:site\']': {content: 'https://rhys.digital/writing/'},
        'link[rel=\'canonical\']': {href: 'https://rhys.digital/writing/'}
      }"
    />

    <PageHeader></PageHeader>

    <section class="section pt-1">
      <div class="mw--800">
        <div class="section__block">
          <div v-if="posts.length !== 0" class="posts">
            <div v-for="post in posts" :key="post.id">
              <div class="post post--card mt-5">
                <h2 class="post__title"><router-link :to="linkResolver(post)">{{ post.data.title }}</router-link></h2>

                <p class="post__date"><strong>Posted:</strong> {{ post.data.posted_date }}</p>
                <p class="post__excerpt">{{ post.data.excerpt[0].text }}</p>
                <router-link :to="linkResolver(post)" exact class="btn"><span class="btn__inner">View post</span></router-link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
// Page header
import PageHeader from '../components/page-header/PageHeader'

export default {
  data: function () {
    return {
      posts: [],
      dateOptions: { year: 'numeric', month: 'short', day: '2-digit' },
      linkResolver: this.$prismic.linkResolver
    }
  },
  components: {
    PageHeader
  },
  created () {
    // Get all posts
    this.getPosts()
  },
  mounted () {
    // eslint-disable-next-line
    setTimeout (() => {
      // Dispatch pageLoad event
      //
      const pageLoad = new CustomEvent('pageLoad')
      document.dispatchEvent(pageLoad)
    }, 250)
  },
  updated () {
    // eslint-disable-next-line
    setTimeout (() => {
      // Dispatch pageLoad event
      //
      const pageLoad = new CustomEvent('pageLoad')
      document.dispatchEvent(pageLoad)
    }, 250)
  },
  methods: {
    getPosts: function () {
      // Query to get blog posts
      this.$prismic.client.query(
        this.$prismic.Predicates.at('document.type', 'post'), { orderings: '[my.post.date desc]' }
      ).then((response) => {
        this.posts = response.results

        this.posts.forEach((post, index) => {
          this.reformatDate(post.data.posted_date, index)
        })
      })
    },
    reformatDate: function (postDate, postIndex) {
      const dateOptions = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' }
      const dateToFormat = new Date(postDate)

      this.posts[postIndex].data.posted_date = dateToFormat.toLocaleDateString('en-US', dateOptions)
    }
  }
}
</script>
